.homeheader{
    color: white !important;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: transparent !important;
}

header{
    color: white;
    background-color: #0C0910;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


header{
    padding: 20px 10px;
}

header img{
    width: 70px;
}

header nav{
    display: flex;
    flex-direction: row-reverse;
}

header nav a{
    margin-right: 15px;
    color: white;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

header nav a:hover{
    color: #73648A;
}

header .barburger{
    display: none;
}

.barburger{
    width: fit-content;
    cursor: pointer;
    display: none;
}

.barburger .slice{
    width: 60px;
    height: 5px;
    background-color: white;
    margin: 10px;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    box-shadow: 1px 2px 8px 0 rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease 0s;
}

@media (max-width:500px) {
    header nav{
        display: flex;
        flex-direction: column;
    }
    nav {
        flex-direction: column;
        text-align: right;
        position: absolute;
        right: -110%;
        background-color: #272337;
        padding: 20px;
        font-size: inherit;
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
        z-index: 5;
        

}

    nav.active{
        right: 0;
        display: flex;
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
        z-index: 5;
        top: 125px;
    }

    nav a{
        margin-bottom: 56px;
        font-size: 31px;
    }
        
    header .barburger{
        display: block;
    }

    body{
        overflow-x:hidden ;
    }

        
    .activeline2{
        position: relative;
        transform: scaleX(0);
        transition: transform 0.3s ease 0s;
        -webkit-transition: transform 0.3s ease 0s;
        -moz-transition: transform 0.3s ease 0s;
        -ms-transition: transform 0.3s ease 0s;
        -o-transition: transform 0.3s ease 0s;
        -webkit-transform: scaleX(0);
        -moz-transform: scaleX(0);
        -ms-transform: scaleX(0);
        -o-transform: scaleX(0);
    }

    .activeline1{
        position: relative;
        transform: rotate(-45deg) translate(-10px, 10px);
        -webkit-transform: rotate(-45deg) translate(-10px, 10px);
        -moz-transform: rotate(-45deg) translate(-10px, 10px);
        -ms-transform: rotate(-45deg) translate(-10px, 10px);
        -o-transform: rotate(-45deg) translate(-10px, 10px);
    }

    .activeline3{
        position: relative;
        transform: rotate(45deg) translate(-10px, -10px);
        -webkit-transform: rotate(45deg) translate(-10px, -10px);
        -moz-transform: rotate(45deg) translate(-10px, -10px);
        -ms-transform: rotate(45deg) translate(-10px, -10px);
        -o-transform: rotate(45deg) translate(-10px, -10px);
    }
    
    nav{
        width: 250px;
        padding: 20px;
        position: absolute;
        overflow-x: hidden;
        top: 125px;
        background-color: #0C0910;
    }
}