.back{
    position: absolute;
    width: 100%;
    z-index: -1;
    height: calc( 100vh - 115px);
    filter: grayscale(0.5);
    -webkit-filter: grayscale(0.5);
}

.profile{
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%,-50%);
    -webkit-transform: translate(50%,-50%);
    -moz-transform: translate(50%,-50%);
    -ms-transform: translate(50%,-50%);
    -o-transform: translate(50%,-50%);
    display: flex;
    background-color: white;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    padding: 15px;
}

.profile img{
    width: 500px;
    height: 400px;
    object-fit: cover;

}

.profile .data{
    padding: 0 0 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: max-content;
}

.profile h1{
    width: max-content;
}

.profile h3{
    font-weight: normal;
    color: #989898;
    margin-bottom: 30px;
}

.profile h3 span{
    color: white;
    background-color: rgb(255, 0, 64);
    padding: 1px 10px;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    font-size: 16px;
}

.profile p{
    margin-top: 10px;
}

.profile p i{
    color: #58c2d5;
}

.profile p span{
    font-weight: bold;
}

.profile .data .buttom i.fa-trash{
    color: rgb(255, 56, 56);
    cursor: pointer;
    font-size: 20px;
    margin-right: 20px;
}

.profile .data .buttom i.fa-user-edit{
    color: rgb(56, 232, 255);
    cursor: pointer;
    font-size: 20px;
}

@media (max-width:768px) {
    .profile{
        flex-direction: column;
    }

    .profile img{
        width: 330px;
    }
  }