/* Start Rules */
*,*::after,*::before{
  -webkit-box-sizing: border-box;
  -o-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html{
  scroll-behavior: smooth;
}

body{
  font-family: 'Open Sans', sans-serif, Arial, Helvetica, sans-serif;
}

.container {
  padding-left:15px;
  padding-right:15px;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width:768px) {
  .container{
      width:750px;
  }
}

@media (min-width:992px) {
  .container{
      width:970px;
  }
}

@media (min-width:1200px) {
  .container{
      width:1170px;
  }
}

ul{
  list-style: none;
}

ol{
  list-style: none;
}

:root{
--main-color:#ffff51;
}

a{
  text-decoration: none;
}

.underline {
  position: relative;
}

.title {
  color: black;
  text-align: center;
  margin-bottom: 50px;
  position: relative;
  font-size: 20px;
  font-weight: bold;
  padding-top: 20px;
}

.underline:after {
  content: "";
  position: absolute;
  width: 125px;
  height: 2px;
  background-color: black;
  bottom: -10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

.underlinewhite:after {
  content: "";
  position: absolute;
  width: 125px;
  height: 2px;
  background-color: white;
  bottom: -10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

.white{
  color: white;
}
/* End Rules */
