.inputs{
    padding: 20px;
    background-color: #041C32;
}



.theform .inputs > div{
    margin-bottom: 20px;
    color: white;
}
.theform .inputs input{
    margin-top: 10px;
    padding: 5px 10px;
    border: none;
    width: 100%;
    border-radius: 3px;
    outline: none;
}

.theform .inputs select{
    margin-top: 10px;
    padding: 5px 10px;
    border: none;
    border-radius: 3px;
    outline: none;
}
.theform .inputs button{
    height: 42px;
    width: 100px;
    background-color: white;
    border: none;
    cursor: pointer;
    border-radius: 3px;
}


.profilephoto{
    position: relative;
}

.profilephoto .fa-pen{
    position: absolute;
    bottom: 60px;
    right: 10px;
    font-size: 25px;
    cursor: pointer;
    display: none;
    color: rgb(87, 87, 15);
}

.profilephoto:hover .fa-pen{
    display: block;
}

[type="file"] {
    height: 0;
    overflow: hidden;
    width: 0;
}

.bar{
    width: 300px;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    -webkit-transform: translateX(50%);
    -moz-transform: translateX(50%);
    -ms-transform: translateX(50%);
    -o-transform: translateX(50%);
    height: 10px;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    z-index: 2;
}
.bar .progress{
    background-color: #2196f3;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    height: 10px;
    z-index: 3;
}
.theform img{
    width: 300px;
    display: block;
    margin: 20px auto;
    padding: 15px;
    background-color: #16141d;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}